/* eslint-disable  */
import axios from 'axios'

const host = 'http://118.ydmhb.com/'
const wx = window.wx

let lock = false

function getAxiosUrl (model) {
  return host + model
}

export function getUrl (url) {
  let token = ''
  if ( sessionStorage.getItem('token') ) {
    token = sessionStorage.getItem('token')
    return host + url + '&token=' + token
  } else {
    login()
  }
}

// 获取数据
export function getAxiosData (mode, data = {}, method = 'GET', is_loading = true) {
  let url = getAxiosUrl(mode)
  method = method.toUpperCase()
  if ( method === 'GET' || method === 'PUT' || method === 'DELETE' ) {
    if ( data && ( data.id || data.id === 0 ) ) {
      url += '/' + data.id
      delete data.id
    }
  }
  if ( method === 'GET' ) {
    data = {
      params: data
    }
  } else {
    data = {
      data: data
    }
  }
  return axios({ method, url, ...data }).then((response) => {
    return checkStatus(response)
  }).then((res) => {
    return checkCode(res)
  }).catch((err) => {
    if ( err.response ) {
      return err.response
    } else if ( err.request ) {
      return err.request
    } else {
      return err.message
    }
  })
}

function checkStatus (response) {
  // loading
  // 如果http状态码正常，则直接返回数据
  if ( response && ( response.status === 200 || response.status === 304 || response.status === 400 ) ) {
    return response
    // 如果不需要除了data之外的数据，可以直接 return response.data
  }
  // 异常状态下，把错误信息返回去
  return {
    status: -404,
    msg: '网络异常'
  }
}

function checkCode (res) {
  // 如果code异常(这里已经包括网络错误，服务器错误，后端抛出的错误)，可以弹出一个错误提示，告诉用户
  if ( res.status === -404 ) {
    // console.log(res.msg)
  }
  if ( res.data && ( !res.data.success ) ) {
    // console.log(res.data.errmsg)
  }
  if ( res.data.errcode === 40001 || res.data.errcode === 40000 ) {
    login()
  }
  return res
}

// 判断是否登录(token)
export function init () {
  let tjid = urlParse('tjid')
  if ( tjid ) {
    sessionStorage.setItem('tjid', tjid)
  }
  let token = urlParse('token')
  if ( token ) {
    window.history.pushState('', '', 'index.html#/brand')
    sessionStorage.setItem('token', token)
  }
  if ( !sessionStorage.getItem('token') ) {
    // login()
    return {
      then: function (fn) {
      }
    }
  } else {
    return config(token)
  }
}

// 登陆
function login () {
  if ( lock ) return
  lock = true
  let url = '/v1/wx/login'
  let tjid = sessionStorage.getItem('tjid')
  let data = {}
  if ( tjid ) {
    data.tjid = tjid
  }
  let state = location.hash
  state = state.replace(/[^|&]token=([^&]*)($|&)/, '')
  data.state = encodeURIComponent(state)
  getAxiosData(url, data).then((res) => {
      if ( res.data.errcode === 0 ) {
        console.log('登陆成功')
        location.href = res.data.url
      } else {
        lock = false
      }
    }
  ).catch(res => {
    lock = false
  })
}

/**
 * 解析获取url参数
 */
function urlParse (name) {
  let search = window.location.hash
  if ( location.search !== '' ) {
    search = location.search
  }
  let index = search.indexOf('?')
  search = search.substr(index)

  let reg = new RegExp('(^|&)' + name + '=([^&]*)($|&)', 'i')
  let res = search.substr(1).match(reg)

  if ( res != null ) {
    return unescape(res[ 2 ])
  }
  return null
}

// 获取分享配置信息
function config (token) {
  let data = {}
  data.token = token
  // 需要分享的页面
  data.url2 = location.href.replace(/&token=.*/, '').replace(/\?token=.*/, '').replace(/&tkid=.*/, '').replace(/\?tjid=.*/, '')
  data.url = location.href.split('#')[ 0 ]
  return getAxiosData('/v1/wx/config', data).then(res => {
    if ( res.data.errcode === 0 ) {
      shareConfig(res.data.data)
    }
  })
}

// 获取微信分享的相关信息配置
function shareConfig (data) {
  if ( typeof data.config === 'string' ) {
    data.config = JSON.parse(data.config)
  }
  data.link = decodeURIComponent(data.link)
  let config = data.config

  wx.config({
    debug: false,
    appId: config.appId,
    timestamp: config.timestamp,
    jsApiList: [ 'onMenuShareTimeline', 'onMenuShareAppMessage' ],
    nonceStr: config.nonceStr,
    signature: config.signature
  })
  wx.ready(function () {
    wx.onMenuShareTimeline({
      title: data.title,
      link: data.link,
      desc: data.desc,
      imgUrl: data.imgUrl,
      success: function () {
        alert('分享成功!')
      },
      cancel: function () {
        // 用户取消分享执行的回调函数
      }
    })
    wx.onMenuShareAppMessage({
      title: data.title,
      link: data.link,
      desc: data.desc,
      imgUrl: data.imgUrl,
      success: function () {
        alert('分享成功!')
      },
      cancel: function () {
        // 用户取消分享执行的回调函数
      }
    })
    wx.error(function (res) {
      // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
    })
  })
}
