

import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import { getAxiosData } from 'common/js/util'
import ClipboardJs from 'clipboard'
import 'reset.less'
import jsonp from 'jsonp'

export default {
name: 'ProductCenter',
data () {
return {
category_index: 0,
editPattern: false,
banner: [ {
url: 'javascript:;',
img: '/static/banner.png'
} ],
list: [],
name: {},
content: ' 编辑模式说明 \r\n1.在本模式下你可以直接点击要推广的产品.\r\n2.选中的所有产品会批量生成推广链接.\r\n3.选完产品点击下方的一键复制即可推广!  ',
contentRow: 0,
isApp: false,
isWechat: false
}
},
created () {
let userAgent = navigator.userAgent
if ( /Html5Plus/.test(userAgent) ) {
this.isApp = true
} else if ( /MicroMessenger/.test(userAgent) ) {
this.isWechat = true
}
this.$loading('加载中')
getAxiosData('v1/produced').then(res => {
this.$loading('加载中', 'close')
let data = res.data.data
this.banner = data.ad
this.list = [
data.list1,
data.list2,
data.list3
]
this.name = data.name
})
},
mounted () {
var clip = new ClipboardJs('#clipboard')
clip.on('success', () => {
this.$toast('复制成功!')
})
clip.on('error', function () {
this.$toast('复制失败!')
})
},
methods: {
shortUrl (url) {
return new Promise((resolve, reject) => {

url = encodeURIComponent(url)
const serve = [
'http://suo.im/api.php?format=jsonp&url=' + url,

]
jsonp(serve[ Math.floor(Math.random() * serve.length) ], {
param: 'callback'
}, function (err, data) {
if ( err ) {
reject(err)
return
}
resolve(data)
})
})

},
itemClick (item_idx, category_idx) {
let list = this.list[ category_idx ]
let item = list[ item_idx ]
if ( !this.editPattern ) {
location.href = item.url
return
}
item.selected = !item.selected

list[ item_idx ] = item
this.list.splice(category_idx, 1, list)
if ( !this.contentObj ) {
this.contentObj = []
}
if ( item.selected ) {
this.shortUrl(item.url).then((data) => {
this.contentRow += 3
this.contentObj[ item.id ] = item.name + '\r\n' + data.url + '\r\n\r\n'
this.content = this.contentObj.join('')
}, (e) => {
console(e)
})
} else {
delete this.contentObj[ item.id ]
this.content = this.contentObj.join('')
this.contentRow -= 3
}
},
editClick () {
if ( !this.editPattern ) {
this.$toast('批量模式复制模式已打开!')
} else {
this.$toast('批量模式复制模式已关闭!')
}
this.editPattern = !this.editPattern
},
changeCategory (index) {
this.category_index = index
}
},
components: {
Swiper,
Flexbox,
FlexboxItem,
XHeader
}
}
